import React from 'react';

import { Card, CardMedia, CardContent, CardActions, Typography, IconButton } from '@material-ui/core';
import { AddShoppingCart } from '@material-ui/icons';

//HOOK useStyle;
import useStyles from './styles';


const Product = ({ product, onAddToCart }) => {
    const classes = useStyles();

  return( 
    <Card className={classes.root}>
        <CardMedia className={classes.media} image={product.assets[0].url} title={product.name}  />
        <CardContent>
            <div className={classes.cardContent }>
                <Typography variant='h5' gutterBottom>
                    {product.name}
                </Typography>
                <Typography variant='h5'>
                    {product.price.formatted_with_symbol}
                </Typography>
            </div>
                {/* use dangerouslySetInnerHTML or write just the product.dscription  */}
            <Typography dangerouslySetInnerHTML={{__html: product.description}} variant="h6" color="textSecondary" />
        </CardContent>

        <CardActions disableSpacing className={classes.cardActions}>
            <IconButton aria-label="Add To Cart" onClick={()=>{
                onAddToCart(product.id, 1);
            }} >
                <AddShoppingCart /> 
            </IconButton>
        </CardActions>
    </Card>
  )};

export default Product;
